/*jshint strict:false */
'use strict';

import angular from 'angular';
import 'angular-resource';
import 'angular-ui-router';
import 'angular-material';
import 'angular-messages';
import 'angular-ui-mask';

import 'module-crud-ui/app/scripts/crud.module.js';
import 'module-security-ui/app/scripts/sec.module.js';
import 'bower:angular-bootstrap@0.13.4';

import MediaSelectDirective from './home/scripts/directives/med.select.dir';
import VirtualSegmentDirective from './home/scripts/directives/med.virtual.segment.view.dir.js';
import HttpRequestService from './home/scripts/services/med.http.request.srv.js';
import SqueezeService from './home/scripts/services/med.squeeze.srv.js';
import RejectionService from './home/scripts/services/med.rejection.srv.js';
import SegmentService from './home/scripts/services/med.segment.srv.js';
import NewMediaService from './home/scripts/services/med.new.media.srv.js';
import dashboardApiCfg from './scripts/config/dashboard.api.cfg';
import mediaroutes from './scripts/config/med.router.cfg';
import financeOAuthCfg from './scripts/config/med.oauth.cfg.js';
import SegmentTransformerService from './home/scripts/services/med.segment.transformer.srv.js';
import RejectionTransformerService from './home/scripts/services/med.rejection.transformer.srv.js';
import TimeBaseTransformerService from './home/scripts/services/med.time.base.transformer.srv.js';
import SqueezeTransformerService from './home/scripts/services/med.squeeze.transformer.srv.js';
import HotStartTransformerService from './home/scripts/services/med.hotstart.transformer.srv.js';

import homeFilters from './home/scripts/filters/med.home.filt.js';


import 'module-layout-ui';

import './templates';

var app = angular.module('media.app',
  [
    'ui.router',
    'security.module',
    'crud.module',
    'layout.module',
    'ngMaterial',
    'aleph-media-ui-templates',
    'homeFilters',
    'ngMessages',
    'ui.mask'
  ]);
/* global System, document */

System.import('jquery').then(function () {
  angular.element(document).ready(function () {
    angular.bootstrap(document.body, [app.name], {
      // strictDi: trueSegment
    });
  });
});

app.run((OAuthToken, $location) => {

  let token = $location.path().substr(1);
  if(typeof token !== 'undefined' && token !== '' && token.indexOf('access_token')===0 ){
    OAuthToken.setToken(token);
  }
});

app
  .config(financeOAuthCfg())
  .config(mediaroutes())
  .config(dashboardApiCfg())

  .directive('mediaSelectDirective', MediaSelectDirective.directiveFactory)
  .directive('mediaVirtualSegment', VirtualSegmentDirective.directiveFactory)
  .service('httpRequestService', HttpRequestService)
  .service('squeezeService', SqueezeService)
  .service('segmentService', SegmentService)
  .service('rejectionService', RejectionService)
  .service('newMediaService', NewMediaService)
  .service('squeezeTransformerService',SqueezeTransformerService)
  .service('segmentTransformerService',SegmentTransformerService)
  .service('rejectionTransformerService',RejectionTransformerService)
  .service('timeBaseTransformerService',TimeBaseTransformerService)
  .service('hotStartTransformerService',HotStartTransformerService);


export default app;
